.card-container{
   display: grid;
   grid-template-columns: auto 1fr;
   gap: 20px;
}

.card{
    /* margin-left: 200px; */
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;
}
.product-item{
    width: 100%;
}
.product-item img{
    width: 98%;
    height: auto;
    border-radius: 10%;
}

.product-name span{
    font-family: "Poppins", sans-serif;
    color: #28232f;
    font-size: 18px;
    font-weight: 700;
    line-height: 1;
}
.product-title span{
    font-family: "Poppins", sans-serif;
    color: #28232f;
    font-size: 16px;
    font-weight: 500;
    line-height: 1.5;
}

.prices{
    display: flex;
    justify-content: safe;
    gap: 10px;
}
.prices span{
    font-family: "Poppins", sans-serif;
    color: red;
    font-size: 14px;
}

.product-newprice{
    font-family: "Poppins", sans-serif;
    font-size: 14px;
}
.product-oldprice{
    font-family: "Poppins", sans-serif;
    text-decoration: line-through;
    font-size: 14px;
}